import {
	hostUrl
} from '@/http/request.js';
import {
	mapState
} from 'vuex';
export default {
	props: {
		indicatorDots: {
			type: Boolean,
			default: true
		},
		autoplay: {
			type: Boolean,
			default: true
		},
		interval: {
			type: Number,
			default: 2000
		},
		duration: {
			type: Number,
			default: 500
		},
		indicatorActiveColor: {
			type: String,
			default: '#FFF'
		},
		circular: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			hostUrl: hostUrl
		}
	},
	computed: {
		...mapState({
			bannerImg:state => state.home.bannerImg
		})
	},
	methods:{
		goDetail(id){
		const navId = id.split('?');
		this.$emit('goDetail',navId[1])
			
		}
	}

}
